/* global document window fetch */

export function getObjectValue(path, object, defaultValue = null) {
  if (typeof path === 'string') {
    return getObjectValue(path.split(/\./), object, defaultValue);
  }

  if (!Array.isArray(path) || path.length === 0) {
    return defaultValue;
  }

  const [key] = path;
  const slicedPath = path.slice(1);

  if (!object || typeof object !== 'object' || !Object.hasOwnProperty.call(object, key)) {
    return defaultValue;
  }

  if (slicedPath.length === 0) {
    return object[key];
  }
  return getObjectValue(slicedPath, object[key], defaultValue);
}

export function getShortMarketName(fullName) {
  if (fullName === 'google-play') {
    return 'gp';
  }
  return fullName;
}

export function toast(text, { type = 'message' } = {}) {
  console.log('[toast]', text);
  if (!window.siiimpleToast) return;
  window.siiimpleToast[type](text);
  fetch('/i18n/activate/en/').then(() => {
    setTimeout(() => {
      window.location.reload();
    }, 2000);
  });
}

// @match        *://www.appannie.com/ru/apps/ios/app/*/details*
// @match        *://www.appannie.com/ru/apps/google-play/app/*/details*
// @match        *://www.appannie.com/ru/apps/*/app/*/details*
// @match        *://www.appannie.com/apps/ios/app/*/details*
// @match        *://www.appannie.com/apps/google-play/app/*/details*
// @match        *://www.appannie.com/apps/*/app/*/details*

export async function runAppannie() {
  const location = document?.location?.href || '';
  if (!location.includes('://www.appannie.com')) return { status: 'ignoreDomain' };
  const regexp = new RegExp('://www.appannie.com/.*apps/.+/app/.+/details');
  if (!regexp.test(location)) return { status: 'ignore' };

  // console.log('[runAppannie] @');
  const { __NEXT_DATA__ } = window;
  const is404Page = document.body.innerText.startsWith('Oops! 404');
  if (!is404Page && getObjectValue('props.pageProps.locale.language', __NEXT_DATA__) !== 'en-US') {
    await toast('Язык страницы не английский, переключаем на английский');
    return { status: 'changeLocale' };
  }
  const pageVal = {
    location: window.location.href,
  };
  const productId = getObjectValue('props.pageProps.app.id', __NEXT_DATA__);
  const productTitle = getObjectValue('props.pageProps.app.name', __NEXT_DATA__);
  if (is404Page || !productId || (productTitle && productTitle === 'Unknown Product')) {
    await fetch(`https://worker.buzz.guru/api/appannie/notFound?location=${document.location.pathname}`, {
      method: 'POST',
      mode: 'no-cors',
    });
    toast('Спаршено 404', { type: 'alert' });
    console.log('Отрепорти эту ссылку в чатик');
    return { status: '404' };
  }

  const fullMarketName = getObjectValue('props.pageProps.market', __NEXT_DATA__);

  if (productId) {
    pageVal.app_id = String(productId);
  }

  if (fullMarketName) {
    pageVal.market_name = fullMarketName;
  }

  const companyId = getObjectValue('props.pageProps.company.id', __NEXT_DATA__);
  const companyName = getObjectValue('props.pageProps.company.name', __NEXT_DATA__);

  if (companyId || companyName) {
    if (!pageVal.obj_nav) {
      pageVal.obj_nav = {};
    }
    if (!pageVal.obj_nav.company) {
      pageVal.obj_nav.company = {};
      if (companyId) {
        pageVal.obj_nav.company.id = String(companyId);
      }
      if (companyName) {
        pageVal.obj_nav.company.name = companyName;
      }
    }
  }

  const shortMarketName = getShortMarketName(fullMarketName);
  const productUniversalProductId = getObjectValue('props.pageProps.unifiedApp.id', __NEXT_DATA__);

  if (productId || shortMarketName || productUniversalProductId) {
    if (!pageVal.obj_nav) {
      pageVal.obj_nav = {};
    }
    if (!pageVal.obj_nav.product) {
      pageVal.obj_nav.product = {};
      if (productId) {
        pageVal.obj_nav.product.id = String(productId);
      }
      if (shortMarketName) {
        pageVal.obj_nav.product.market = shortMarketName;
      }
      if (productUniversalProductId) {
        pageVal.obj_nav.product.universal_product_id = String(productUniversalProductId);
      }
    }
  }

  const universalPoductId = getObjectValue('props.pageProps.unifiedApp.id', __NEXT_DATA__);
  const universalPoductName = getObjectValue('props.pageProps.unifiedApp.name', __NEXT_DATA__);
  const universalPoductIcon = getObjectValue('props.pageProps.unifiedApp.icon', __NEXT_DATA__);

  if (universalPoductId || universalPoductName || universalPoductIcon) {
    if (!pageVal.obj_nav) {
      pageVal.obj_nav = {};
    }
    if (!pageVal.obj_nav.universal_product) {
      pageVal.obj_nav.universal_product = {};
      if (universalPoductId) {
        pageVal.obj_nav.universal_product.id = String(universalPoductId);
      }
      if (universalPoductName) {
        pageVal.obj_nav.universal_product.name = universalPoductName;
      }
      if (universalPoductIcon) {
        pageVal.obj_nav.universal_product.icon = universalPoductIcon;
      }
    }
  }

  const publisher = getObjectValue('props.pageProps.publisher', __NEXT_DATA__);
  if (publisher) {
    pageVal.publisher = publisher;
  }

  const appName = getObjectValue('props.pageProps.app.name', __NEXT_DATA__);
  const appIcon = getObjectValue('props.pageProps.app.icon', __NEXT_DATA__);
  const _market = getObjectValue('props.pageProps.app.market', __NEXT_DATA__);
  const status = getObjectValue('props.pageProps.app.status', __NEXT_DATA__);
  let market;
  if (_market === 'google-play') market = 'gp';
  if (_market === 'apple-store') market = 'ios';
  const storeUrl = getObjectValue('props.pageProps.app.url', __NEXT_DATA__);
  let marketId;
  if (market === 'ios') {
    const [, id] = /\/id(\d+)/.exec(storeUrl);
    marketId = id;
  }
  if (market === 'gp') {
    const url = new URL(storeUrl);
    const urlParams = new URLSearchParams(url.search);
    marketId = urlParams.get('id');
  }
  pageVal.meta_data = {};
  if (appName) {
    pageVal.meta_data.name = appName;
  }
  if (appIcon) {
    pageVal.meta_data.icon = appIcon;
  }
  if (market) {
    pageVal.meta_data.market = market;
  }
  if (marketId) {
    pageVal.meta_data.marketId = marketId;
  }
  if (typeof status === 'number') {
    pageVal.meta_data.status = status;
  }
  try {
    const res =
      (await fetch('https://worker.buzz.guru/api/appannie/updateOne', {
        method: 'post',
        headers: {
          Accept: 'application/json',
          'Content-Type': 'application/json',
        },
        body: JSON.stringify(pageVal),
      })) || {};
    console.log({ status: res.status, statusText: res.statusText });
    window.siiimpleToast.success('Приложение спаршено');
  } catch (e) {
    window.siiimpleToast.alert('Произошла ошибка при запросе к воркеру');
  }
  return { status: 'ok' };
}

export default runAppannie;
