/* eslint-disable no-console */
/* global document */
import ready from '@lskjs/utils/polyfill';
import Bluebird from 'bluebird';
import React from 'react';
import ReactDOM from 'react-dom';

import { App } from './App';
import { runAppannie } from './runAppannie';
import { runInstagramIntercept } from './runInstagramIntercept';
import { runModashIntercept } from './runModashIntercept';
import { injectCss, injectJs } from './utils';

ready();

const jses = ['https://cdn.jsdelivr.net/npm/siiimple-toast/dist/siiimple-toast.min.js'];
const csses = [
  'https://cdn.jsdelivr.net/npm/bootstrap@5.1.3/dist/css/bootstrap.min.css',
  'https://cdn.jsdelivr.net/npm/siiimple-toast/dist/style.css',
];

const main = async () => {
  try {
    await Bluebird.map(jses, (item) => injectJs(item));
    await Bluebird.map(csses, (item) => injectCss(item));
  } catch (error) {
    console.error(error);
  }

  const node = document.createElement('div');
  ReactDOM.render(React.createElement(App), node);
  document.body.appendChild(node);
  // setTimeout(() => {
  //   openTab('https://www.instagram.com/elena__valereevna/');
  // }, 2000);

  // window.__lskjs.GM_xmlhttpRequest({
  //   method: 'GET',
  //   url: 'https://worker.buzz.guru/api/healthcheck',
  //   onload() {
  //     console.log('onload!!!');
  //   },
  //   onerror() {
  //     console.error('onerror!!!');
  //   },
  // });

  runAppannie()
    .then((res) => {
      console.log('[runAppannie] ok', res);
    })
    .catch((err) => {
      console.error('[runAppannie] err', err);
    });
  runInstagramIntercept()
    .then((res) => {
      console.log('[runInstagramIntercept] ok', res);
    })
    .catch((err) => {
      console.error('[runInstagramIntercept] err', err);
    });
  runModashIntercept()
    .then((res) => {
      console.log('[runModashIntercept] ok', res);
    })
    .catch((err) => {
      console.error('[runModashIntercept] err', err);
    });
};

main();
