/* eslint-disable no-param-reassign */
/* global document window XMLHttpRequest */

// @match        *://modash.com/*
// @match        *://*.modash.com/*

const isDev = 0;
const host = isDev ? 'http://127.0.0.1:8000' : 'https://worker.buzz.guru';

function getType(url) {
  const webOverviewRegexp = /^.*\/api\/discovery\/show-profile.*$/;
  const webFullRegexp = /^.*\/api\/discovery\/report.*$/;
  const isWebOverview = webOverviewRegexp.test(url);
  const isWebFull = webFullRegexp.test(url);

  let type = '';
  if (isWebOverview) type = 'web_overview';
  if (isWebFull) type = 'web_full';
  return type;
}

function getRoute(profile) {
  const { profileType } = profile;

  let platform = '/';
  if (profileType === 'YOUTUBE') platform = 'youtube';
  if (profileType === 'INSTAGRAM') platform = 'instagram';
  if (profileType === 'TIKTOK') platform = 'tiktok';

  // return `modash/channel_events/${platform}/`;
  return `${platform}_channel_modash_events/`;
}

function addXMLRequestCallback(callback) {
  let oldSend;
  let oldSetRequestHeader;
  let oldOpen;
  let i;
  if (XMLHttpRequest.callbacks) {
    XMLHttpRequest.callbacks.push(callback);
  } else {
    XMLHttpRequest.callbacks = [callback];
    oldSend = XMLHttpRequest.prototype.send;
    oldSetRequestHeader = XMLHttpRequest.prototype.setRequestHeader;
    oldOpen = XMLHttpRequest.prototype.open;
    XMLHttpRequest.prototype.send = function (body) {
      this.meta.body = body;
      oldSend.apply(this, arguments); // eslint-disable-line prefer-rest-params
    };
    XMLHttpRequest.prototype.open = function (method, url, async, user, password) {
      this.meta = {
        method,
        url,
        async,
        user,
        password,
      };
      this.addEventListener('load', function () {
        for (i = 0; i < XMLHttpRequest.callbacks.length; i += 1) {
          // eslint-disable-line no-plusplus
          XMLHttpRequest.callbacks[i](this);
        }
      });
      oldOpen.apply(this, arguments); // eslint-disable-line prefer-rest-params
    };
    XMLHttpRequest.prototype.setRequestHeader = function (key, value) {
      if (!this.requestHeaders) {
        this.requestHeaders = {};
      }
      this.requestHeaders[key] = value;
      oldSetRequestHeader.apply(this, arguments); // eslint-disable-line prefer-rest-params
    };
  }
}

export async function runModashIntercept() {
  const location = document?.location?.href || '';
  if (!location.includes('marketer.modash.io')) return { status: 'ignoreDomain' };

  addXMLRequestCallback(async (xhr) => {
    const responseHeaders = {};
    const responseHeadersArray = xhr
      .getAllResponseHeaders()
      .split('\n')
      .filter((str) => str);
    responseHeadersArray.forEach((header) => {
      const [key, value] = header.split(/: (.+)/);
      responseHeaders[key] = value;
    });
    const payload = {
      url: xhr.meta.url,
      request: {
        method: xhr.meta.method,
        headers: xhr.requestHeaders,
        body: xhr.meta.body,
      },
      response: {
        headers: responseHeaders,
        text: xhr.responseText,
      },
    };

    const type = getType(payload.url);
    if (!type) return;

    const { error, profile } = JSON.parse(payload.response.text) || {};
    if (error || !profile) return;

    const route = getRoute(profile);
    if (!route) return;

    console.log('[R]', payload); // eslint-disable-line no-console

    window.__lskjs.GM_xmlhttpRequest({
      method: 'POST',
      url: `${host}/api/${route}`,
      headers: {
        Accept: 'application/json',
        'Content-Type': 'application/json',
      },
      type,
      data: JSON.stringify(profile.profileData),
    });
  });
  return { status: 'success' };
}

export default runModashIntercept;
